@import "../../styles/mixins/media.scss";

.contactsBlock {
  background-color: var(--darkAccent);
  padding: 40px 0;

  @include desktop {
    width: 300px;
    height: min-content;
    border-radius: 20px;
    padding: 30px;
  }
}

.link {
  display: flex;
  color: var(--white);
  text-decoration: none;
  transition: var(--defaultTransition);
  margin-bottom: 15px;
  font-size: 14px;
  font-style: normal;

  &:hover,
  &:focus {
    text-decoration: underline;
  }
}

.tel {
  font-size: 18px;
  line-height: 20px;
}
